import React, {useContext, useEffect, useState} from 'react';
import {observer} from "mobx-react-lite";
import {Button, Card, Col, Container, Dropdown, Image, Row} from "react-bootstrap";
import {useParams} from "react-router-dom";
import {fetchOneAppeal, updateStatus} from "../http/AppealApi";
import DropdownToggle from "react-bootstrap/DropdownToggle";
import DropdownMenu from "react-bootstrap/DropdownMenu";
import {Context} from "../index";
import Mailer from "./Mailer";

const AppealItem = observer(() => {
    const {user} = useContext(Context)
    const {id} = useParams();
    const [oneAppeal, setOneAppeal] = useState({})
    const [chooseVisible, setChooseVisible] = useState(false)


    useEffect(()=> {
        fetchOneAppeal(id).then(data => {
            setOneAppeal(data)
            setOneAppeal(data)
        })
        },[])
    console.log(oneAppeal)

    const updStatus = async (status) => {
        try {
            console.log(status)
            updateStatus({status: status},id).then()
            window.location.reload()
        }catch (e) {
            console.error(e.message)
        }
    }


    return (
        <Container className='d-flex flex-column'>
            <Card
                style={{width: 1130, height: 350, border: '5px solid lightgray'}}
                className='mt-3 p-2'
            >
                <Row className='d-flex flex-row align-items-start justify-content-center mt-3'>
                    <Col md={4}>
                        <div style={{background:'lightgray'}} className='d-flex mt-3'>  : {oneAppeal?.status == 'notreviewed'? " " : null}</div>
                        <div style={{background:'transparent'}} className='d-flex mt-3'> : {oneAppeal?.name} {oneAppeal?.surname}</div>
                        <div style={{background:'lightgray'}} className='d-flex mt-3'>  : {oneAppeal?.home_address} </div>
                        <div style={{background:'transparent'}} className='d-flex mt-3'> Email : {oneAppeal?.user?.email}</div>
                        {oneAppeal?.nameLegal?
                            <div style={{background:'lightgray'}} className='d-flex mt-3'>   : {oneAppeal?.nameLegal? oneAppeal?.nameLegal : null}</div>
                            :
                            null
                        }
                    </Col>
                    <Col md={4}>
                        <Image style={{width: 300, height: 300}} src={'http://localhost:5000/' + oneAppeal?.img}/>
                    </Col>
                    <Col md={4} >
                        <div className='d-flex flex-column justify-content-between mt-3 mb-3'>
                            <span >    </span>
                            <Button className='mt-5' variant={"outline-success"} href={'http://localhost:5000/' + oneAppeal?.file}> </Button>
                            {
                                user.role === 'EMPLOYEE'?
                                    <div>
                                        <Dropdown className='mt-3'>
                                            <DropdownToggle>{oneAppeal?.status === 'notreviewed'? ' ' : oneAppeal?.status === 'viewed'? '  ' : oneAppeal?.status === 'reviewed'? '' : ' '}</DropdownToggle>
                                            <DropdownMenu>
                                                <Dropdown.Item onClick={() => updStatus('notreviewed')} > </Dropdown.Item>
                                                <Dropdown.Item onClick={() => updStatus('viewed')}>  </Dropdown.Item>
                                                <Dropdown.Item onClick={() => updStatus('reviewed')}></Dropdown.Item>
                                            </DropdownMenu>
                                        </Dropdown>
                                        <Button variant={"outline-dark"} className='mt-3 p-2' onClick={() => setChooseVisible(true)}>   </Button>
                                        <Mailer oneAppeal={oneAppeal} show={chooseVisible} onHide={() => setChooseVisible(false)} />
                                    </div>
                                    :
                                    null
                            }
                        </div>
                    </Col>
                </Row>
            </Card>
            <br/>
            <Row>
                <div className='d-flex'>  : {oneAppeal?.content}</div>
            </Row>
        </Container>
    );
});

export default AppealItem;